*{
  margin:0;
  padding: 0;
  list-style: none;
  text-decoration: none;
}
body{
  background: #f7f3ed;
}
.flex{
  display:flex;
}
.desktopMenuItems li {
  margin-right: 50px;
}
.desktopMenuItems li a{
  color:#000;
  text-decoration: none;
}

.logo{
  display: flex;
  align-items: center;
  width: 100%;
}
.logo a img{
  width:150px;
  height: 40px;
}
.desktopMenu{
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  font-size: 1.125rem;
  letter-spacing: .0375rem;
}
header{
  position: fixed;
  top: 0;
  z-index: 9;
  padding-top: 1.2rem;
  padding-bottom: 1.2rem  ;
  width: 100%;
}
.fullflex{
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
header ul{
  padding: 0 4rem;
  margin-bottom: 0px;
}
.homeHeader{
  display: flex;
  position: absolute;
  z-index: 2;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
  font-weight: 400;
}
.homeHeader div{
  padding-top: 4rem;
  text-align: center;
}
.homeHeader h2{
  font-size: 4rem;
  line-height: 4.375rem;
  letter-spacing: .0625rem;
}
.homeHeader span{
  display: block;
  text-align: center;
  margin-top: 3rem;
  margin-bottom: 2rem;
  font-size: 1.25rem;
  line-height: 2rem;
}
.btn-1{
  padding: 1rem 3rem;
  border-color: #e04e39;
  color:#fff;
  background: #e04e39;
  font-size: 1.5rem;
  border-radius: 30px;
  border:0px;

}
footer{
  padding-top: 4rem;
  padding-bottom: 4rem;
  background: #fff;
}
.container{
  width: 100%;
  max-width: 100%;
  padding: 0 4rem;
}
footer ul{
  padding:0px;
}
footer h4{
  font-size: 2rem;
  font-family: cursive;
}
footer ul li a{
  color:#000;
  text-decoration: none;
}
.sociallist{
  display: flex;
}
.sociallist li{
  font-size: 2rem;
  color: #fff;
  background-color: #000;
  -webkit-border-radius: 50%;
  margin-right: 1rem;
  height: 3rem;
  text-align: center;
  width: 3rem;
}
#homeBg{
  width: 100%;
}
#homeCategories{
  background: linear-gradient(180deg,#b4b58a 0,#f7f3ed 100%);
  padding-top: 2rem;
  padding-bottom: 2rem;
}
.homeCategoryItem{
  position: relative;
  overflow: hidden;
}
.homeCategoryItem img{
  width: 100%;
  border-radius: 10px;
  transition: all 1.5s;
}
.homeCategoryItem .homeitemcontent{
  position: absolute;
  bottom: 10px;
  width: 100%;
  color: #fff;
  display: flex;
  padding-left: 10px;
  box-sizing: border-box;
  justify-content: space-between;
}
.homeCategoryItem .shadow{
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.2);
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 10px;
  cursor: pointer;
}
.homeCategoryItem:hover img{
  transform: scale(1.2);
}
.homeCategoryItem svg {
  margin-right: 10px;
  font-size: 3rem;
}
#HomeSlideMain{
  display: flex;
  flex-direction: column;
  background-color: #fab995;
  width: 100%;
  padding-bottom: 5rem;
}
#HomeSlideMain .navigation{
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 4rem;
  padding-bottom: 3rem;
}
#HomeSlideMain .navigation button{
  background-color: transparent;
  border-color: transparent;
  color: #271919;
  padding: 10px 20px;
}
#HomeSlideMain .navigation button.active{
  border-color: #e04e394d;
  color: #fff;
  border-radius: 30px;
  border: 1px solid;
}
#HomeSlideMain img{
  transition: transform 1s ease-in-out;
  aspect-ratio: 1/1;
  object-fit: cover;
  border-radius: 50%;
  box-shadow: 0 0 20px #381d1d14;
  max-height: 600px;
  max-width: 600px;
}
#HomeSlideMain .content-pane{
  display: flex;
  z-index: 2;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
  font-family: Euclid Flex,Verdana,Arial,Helvetica,sans-serif;
  font-weight: 400;
}
#HomeSlideMain .content-area span{
  display: block;
  margin-bottom: 1rem;
  font-size: 1.5rem;
}
#HomeSlideMain .content-area{
  text-align: center;
}
#HomeSlideMain h2{
  font-size: 4rem;
  margin-bottom: 3rem;
  line-height: 4.375rem;
  letter-spacing: .0625rem;
  font-family: cursive;
}
.title{
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: F37 Attila,Verdana,Arial,Helvetica,sans-serif;
  margin-bottom: 1.5rem;
}
.title a {
  color:#000;
  font-size: 1.2rem;
}
#HomeProducts{
  padding-top: 4rem;
  padding-bottom: 4rem;
}
#HomeProducts img{
  width: 100%;
  border-radius: 10px;
  cursor: pointer;
}
.productTitleGrid{
  display: block;
  font-size: 1.5rem;
  text-align: center;
  margin-top: 1rem;
  margin-bottom: 0.5rem;
  color:#000;
  text-decoration: none;
}
.colorsgrid{
  display: flex;
  text-align: center;
  justify-content: center;
}
.colorsgrid span{
  border: 1px solid #e2ddd8;
  width: 13px;
  height: 13px;
  margin-right: 2px;
  cursor: pointer;
  border-radius: 50%;
}
.colorwhite{
  background: #fff;
}
.colorpurple{
  background: purple;
}
.colorblack{
  background: black;
}
.colorpink{
  background: rgb(242, 201, 206);
}
.content-section{
  display: flex;
  flex-direction: column;
  height: 100%;
  min-height: calc(100vh - 180px);
  padding-top:8rem;
}
.container2{
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
}
.collectionsTop{
  display: flex;
  justify-content: space-between;
  font-family: Euclid Flex,Verdana,Arial,Helvetica,sans-serif;
  margin-top: 2rem;
  margin-bottom: 2rem;
}
.collectionsTop input{
  box-sizing: border-box;
  border: 1px solid transparent;
  font-size: 1rem;
  padding: 10px 20px;
  line-height: 1.625rem;
  overflow: hidden;
  text-decoration: none;
  position: relative;
  margin: 0;
  display: block;
  background: #fff;
  border-radius: 64px;
  color: #271919;
}
.collectionsTop button{
  background-color: transparent;
  border-color: transparent;
  color: #271919;
  padding: 10px 20px;
  border: none;
}
.collectionsTop button.active{
  border-color: #e04e394d;
  color: red;
  border-radius: 30px;
  border: 1px solid;
}
.collectionsTop .icon{
  margin-right: 1rem;
  font-size: 2rem;
  color: #f0c2b7;
  cursor: pointer;
}
.gridProductImage1{
  width: 100%;
  cursor: pointer;
}
.gridItem{
  margin-bottom:2rem;
}
#productInfo{
  min-height: 750px;
  margin-left: 2rem;
  padding: 2rem;
  background: #fff;
  margin-bottom: 2rem;
  border-radius: 20px;
}
#productImage{
  max-height: 750px;
  background-position: center center;
  background-size: cover;
  border-radius: 20px;
}
.productcol2,.productcol1{
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 5rem;
  height: 100%;
}
.productcol2{
  height: 502px;
  margin-bottom: 2rem;
}
.submenuImage{
  width:50px;
  height: 50px;
}
#subMenu{
  visibility: hidden;
  background: #f3f3f3;
  position: fixed;
  z-index: -5;
  margin-left: -32rem;
  padding-top: 3rem;
}
.submenuItem{
margin-bottom: 2rem;
display: flex;

}
.submenuItem span{
margin-left: 2rem;
}
.submenuItem a{
border:1px solid #e6e6e6;
display: block;
width: 100%;
border-radius: 10px;
}
.desktopMenuItems li:hover #subMenu{
  visibility: visible;
}